.card-slider-container{position:relative;display:block;overflow-x:hidden;width:100%;min-width:20rem;height:auto;padding:1.6rem}.card-slider-container .card-slider__list{display:flex;flex-direction:row;align-items:flex-start;justify-content:flex-start;width:max-content;height:auto;margin:0 auto;transition:transform .25s ease-out}.card-slider-container .card-slider__slide-btn{position:absolute;display:none;align-items:center;justify-content:center;width:4.6rem;height:4.6rem;border-radius:4.6rem;border:1px solid var(--divider-color);background-color:var(--background-0);box-shadow:0 0 8px 0px rgba(0,0,0,.1);font-size:2.8rem;font-weight:600;user-select:none;top:50%;transform:translateY(-50%)}.card-slider-container .card-slider__slide-btn.left{left:1rem}.card-slider-container .card-slider__slide-btn.right{right:1rem}.card-slider-container.with-cta{border:1px solid var(--divider-color);background-color:var(--card-slider-bg-color);padding:1.6rem 6.6rem}.card-slider-container.with-cta .card-slider__slide-btn{display:inline-flex}